@use "global" as *; 

.movie {
  margin-bottom: rem(80);

  &__inner {
    max-width: $inner;
    padding: 0 rem(16);
    padding-top: rem(5);
  }

  &__title {
    font-family: $en-font;
    font-size: rem(21);
    font-weight: $regular;
    margin-bottom: rem(24);

    span {
      position: relative;
      padding-left: rem(30);

      &::before {
        content: "";
        width: rem(10);
        height: rem(1);
        background: #444;
        position: absolute;
        top: 50%;
        left: rem(10);
        transform: translateY(-50%);
      }

      &::after {
        content: "";
        width: rem(10);
        height: rem(1);
        background: #444;
        position: absolute;
        top: 50%;
        right: rem(-20);
        transform: translateY(-50%);
      }
    }
  }

  &__images {
    // max-width: rem(320);
    // max-height: rem(240);
    width: 100%;
    height: rem(200);

    iframe {
      width: 100%;
      height: 100%;
    }
  }
}

@include mq (md) {
  .movie {
    &__inner {
      margin: 0 auto;
    }

    &__title {
      font-size: rem(32);
    }

    &__images {
      height: rem(600);
      border-radius: rem(10);

      iframe {
        width: 100%;
        height: 100%;
        // object-fit: cover;
        // max-height: rem(400);
        border-radius: rem(10);
      }
    }
  }
}